@font-face {
  font-family: 'LightsOut';
  src: local('LightsOut'), url(./fonts/LightsOut.woff) format('woff');
}
@font-face {
  font-family: 'FuturaNowHeadline';
  src: local('FuturaNowHeadline'), url(./fonts/futuranowheadline-webfont.woff) format('woff');
}
@font-face {
  font-family: 'FuturaNowHeadlineLight';
  src: local('FuturaNowHeadlineLight'), url(./fonts/futuranowheadlinelight-webfont.woff) format('woff');
}
@font-face {
  font-family: 'FuturaNowHeadlineMedium';
  src: local('FuturaNowHeadlineLight'), url(./fonts/futuranowheadlinemedium-webfont.woff) format('woff');
}

body {
  margin: 0; /* Remove default margin to ensure full viewport coverage */
  padding: 0; /* Remove default padding */
  background-color: #000000;
  color: #FFFFFF;
}
