.hand-image {
    position: fixed;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    max-height: 100svh;
    max-width: 100svw;
    z-index: 1;
}

.hand-input {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: transparent;
    border: none;
    color: #FFFFFF;
    outline: none;
    text-align: center;
    font-family: 'LightsOut';
    font-size: clamp(10vw, 20vw, 10em);
    line-height: 1.8;
    padding-bottom: 10px;
    max-width: 100vw;
    text-shadow: 2px 2px 4px black;
    caret-color: transparent;
    z-index: 2;
}

.hand-input::selection{
    background: transparent;
}

/* Define the fade-in animation */
@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

/* Apply the animation to the input field */
.fade-out-input {
    animation: fadeOut 2s both;
}

.animated-image {
    animation: shakeScaleFade 3s cubic-bezier(.36,.07,.19,.97), zoomOut 0.5s 4s linear both;
}

@keyframes shakeScaleFade {
    0%{
        transform: rotate(0deg);
    }
    10%{
        transform: rotate(-1deg);
    }
    20%{
        transform: rotate(1deg);
    }
    30%{
        transform: rotate(-2deg);
    }
    40%{
        transform: rotate(2deg);
    }
    50%{
        transform: rotate(-3deg);
    }
    60%{
        transform: rotate(3deg);
    }
    70%{
        transform: rotate(-4deg);
    }
    80%{
        transform: rotate(4deg);
    }
    90%{
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0);
    }
}

@keyframes zoomOut {
    to {
        transform: scale(20);
        opacity: 0;
    }
}